<script setup lang="ts">
import { ref, reactive, onMounted, onUnmounted } from 'vue'

const props = defineProps<{ photos: string[], loading?: boolean }>()
const lightbox = ref<string | null>(null)
const imageLoaded = reactive<Record<string, boolean>>({})

function openLightbox(url: string) { lightbox.value = url }
function closeLightbox() { lightbox.value = null }
function onImageLoad(url: string) { imageLoaded[url] = true }

function nextImage() {
  if (!lightbox.value) return
  const idx = props.photos.indexOf(lightbox.value)
  if (idx < props.photos.length - 1) lightbox.value = props.photos[idx + 1]
}

function prevImage() {
  if (!lightbox.value) return
  const idx = props.photos.indexOf(lightbox.value)
  if (idx > 0) lightbox.value = props.photos[idx - 1]
}

function onKeydown(e: KeyboardEvent) {
  if (!lightbox.value) return
  if (e.key === 'Escape') closeLightbox()
  if (e.key === 'ArrowRight') nextImage()
  if (e.key === 'ArrowLeft') prevImage()
}

onMounted(() => window.addEventListener('keydown', onKeydown))
onUnmounted(() => window.removeEventListener('keydown', onKeydown))
</script>

<template>
  <!-- Loading Skeletons -->
  <div v-if="loading" class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
    <div v-for="i in 10" :key="i" class="aspect-square bg-white/5 rounded-xl animate-pulse"></div>
  </div>

  <!-- Gallery Grid -->
  <div v-else-if="photos.length" class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
    <div 
      v-for="url in photos" 
      :key="url" 
      @click="openLightbox(url)" 
      class="group relative aspect-square rounded-xl overflow-hidden cursor-pointer bg-white/5"
    >
      <!-- Image -->
      <img 
        :src="url" 
        @load="onImageLoad(url)" 
        class="w-full h-full object-cover transition-transform duration-700 ease-out group-hover:scale-110" 
        :class="{ 'opacity-0': !imageLoaded[url] }" 
        loading="lazy" 
      />
      
      <!-- Premium Overlay -->
      <div class="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-end p-4">
         <div class="translate-y-4 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all duration-300 delay-75">
            <p class="text-white text-xs font-medium truncate w-full">{{ url.split('/').pop() }}</p>
         </div>
      </div>
    </div>
  </div>

  <!-- Empty State -->
  <div v-else class="h-96 flex flex-col items-center justify-center text-gray-500">
    <div class="w-16 h-16 rounded-2xl bg-white/5 flex items-center justify-center mb-4">
       <svg class="w-6 h-6 opacity-50" fill="none" viewBox="0 0 24 24" stroke="currentColor">
         <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
       </svg>
    </div>
    <p class="text-sm">No photos found</p>
  </div>

  <!-- Cinematic Lightbox -->
  <Teleport to="body">
    <Transition name="lightbox">
      <div v-if="lightbox" class="fixed inset-0 z-[100] bg-black/95 backdrop-blur-xl flex items-center justify-center" @click.self="closeLightbox">
        
        <!-- Controls -->
        <button @click="closeLightbox" class="absolute top-6 right-6 p-4 text-white/50 hover:text-white transition-colors">
            <svg class="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" /></svg>
        </button>

        <button v-if="photos.indexOf(lightbox) > 0" @click.stop="prevImage" class="absolute left-6 p-4 text-white/50 hover:text-white hover:scale-110 transition-all">
            <svg class="w-8 h-8" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" /></svg>
        </button>

        <button v-if="photos.indexOf(lightbox) < photos.length - 1" @click.stop="nextImage" class="absolute right-6 p-4 text-white/50 hover:text-white hover:scale-110 transition-all">
            <svg class="w-8 h-8" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" /></svg>
        </button>
        
        <!-- Main Image -->
        <img :src="lightbox" class="max-w-[95vw] max-h-[90vh] object-contain rounded-lg shadow-2xl animate-scale-up" />
        
        <!-- Caption -->
        <div class="absolute bottom-8 left-1/2 -translate-x-1/2 px-6 py-3 bg-white/10 backdrop-blur-md rounded-full border border-white/10 text-sm text-white/80">
           {{ lightbox.split('/').pop() }}
        </div>
      </div>
    </Transition>
  </Teleport>
</template>

<style scoped>
.lightbox-enter-active, .lightbox-leave-active { transition: opacity 0.3s cubic-bezier(0.16, 1, 0.3, 1); }
.lightbox-enter-from, .lightbox-leave-to { opacity: 0; }

@keyframes scaleUp {
    from { transform: scale(0.95); opacity: 0; }
    to { transform: scale(1); opacity: 1; }
}
.animate-scale-up { animation: scaleUp 0.4s cubic-bezier(0.16, 1, 0.3, 1); }
</style>
